<template>
  <section id="Product">
    <v-container>
      <v-row>
        <v-col cols="12" md="6">
          <!-- START:__GALLERY____ -->
          <div class="gallery mb-2">
            <v-fade-transition>
              <v-img
                :src="require('../../assets/images/' + currentImage + '.jpg')"
                contain
                max-height="400"
              ></v-img>
            </v-fade-transition>
          </div>
          <!-- START:____SUB__GALLERY____ -->
          <div class="sub-gallery px-5 mt-4">
            <v-row justify="center">
              <v-col
                class="pa-1 justify-center"
                style="cursor: pointer"
                @click="currentImage = item"
                cols="3"
                sm="2"
                md="3"
                v-for="(item, i) in product.gallery"
                :key="i"
              >
                <v-img
                  max-width="130"
                  contain
                  :src="require('../../assets/images/' + item + '.jpg')"
                ></v-img>
              </v-col>
            </v-row>
          </div>
        </v-col>
        <!-- START:__INFORMATION____ -->
        <v-col cols="12" md="6">
          <v-card
            max-width="500"
            flat
            class="d-flex mx-auto flex-column text-center text-md-left align-center align-md-start justify-center"
          >
            <v-card-text>Home/{{ product.title }}</v-card-text>
            <v-card-text class="text-h4 pb-0 font-weight-black">{{
              product.title
            }}</v-card-text>
            <v-card-subtitle>${{ product.price }}</v-card-subtitle>
            <v-card-text class="py-0">
              <div class="d-block mx-auto mx-md-0" style="width: 30%">
                <v-select outlined dense class="" :items="items.selectSize">
                  <template v-slot:label>
                    <p class="caption">Select Size</p></template
                  ></v-select
                >
              </div>
            </v-card-text>
            <v-card-text
              class="d-flex align-center justify-center justify-md-start"
            >
              <div style="width: 20%">
                <v-text-field
                  outlined
                  type="number"
                  dense
                  v-model="quantity"
                ></v-text-field>
              </div>
              <v-card-actions class="mb-6">
                <v-btn
                  @click="addToCart(product)"
                  rounded
                  class="px-5"
                  block
                  color="error"
                >
                  <span>add to cart</span>
                  <v-icon>mdi-cart</v-icon>
                </v-btn>
              </v-card-actions>
            </v-card-text>
            <v-card-title class="mb-3 text-uppercase text-center text-md-left">
              <span> product details </span>
              <v-icon right>mdi-package-variant-closed-minus</v-icon>
            </v-card-title>
            <v-card-subtitle>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Quam
              delectus est distinctio voluptatum. Quod, vitae tenetur dolorum
              nam libero laboriosam repudiandae. Ullam suscipit cum, aperiam
              dignissimos itaque veritatis asperiores voluptatem!
            </v-card-subtitle>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>
<script>
import data from "@/db/data.json";
import { mapActions } from "vuex";
export default {
  data: () => ({
    productId: "null",
    currentImage: "gallery-1",
    items: data,
    product: [],
    quantity: 1,
  }),
  mounted() {
    // this.productId = this.$route.params.id;
    this.productId = this.id;
    this.product = this.items.latest.filter((el) => el.id == this.productId)[0];
    window.scrollTo({
      behavior: "smooth",
      // top: 0,
    });
    if (!this.product.gallery) {
      this.currentImage = this.product.src;
    }
  },
  props: ["id"],
  methods: {
    ...mapActions(["A_addToCart"]),
    addToCart(product) {
      this.A_addToCart({
        product: product,
        quantity: this.quantity,
      });
    },
  },
};
</script>
<style lang="scss" scoped></style>
